// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import VueAnime from 'vue-animejs'
import axios from 'axios'
import VueAxios from 'vue-axios'
import { library } from '@fortawesome/fontawesome-svg-core'
import { faUserSecret,faPhoneVolume,faCompass,faStar,faHeart,faShoppingCart,faTimesCircle,faMapSigns,
faEnvelopeOpen, faClock,
} from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'




library.add(faUserSecret, faPhoneVolume, faCompass, faStar, faHeart, faShoppingCart, faTimesCircle, 
  faEnvelopeOpen, faMapSigns, faClock,)



Vue.component('font-awesome-icon', FontAwesomeIcon)
Vue.use(VueAxios, axios)

Vue.use(VueAnime)


Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



